<h2>属性</h2>
<h3><code v-pre>vc-geometry-wall</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre>required</code> 指定 wall 位置数组。 <strong>structure: [{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定每个纬度和经度之间的距离（弧度）。</td>
</tr>
<tr>
<td>maximumHeights</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> 指定 wall 顶部的高度数组。</td>
</tr>
<tr>
<td>minimumHeights</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> 指定 wall 底部的高度数组。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定参考椭球体。</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定顶点属性渲染方式。</td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre>vc-geometry-outline-wall</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre>required</code> 指定 wall 位置数组。 <strong>structure: [{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定每个纬度和经度之间的距离（弧度）。</td>
</tr>
<tr>
<td>maximumHeights</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> 指定 wall 顶部的高度数组。</td>
</tr>
<tr>
<td>minimumHeights</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> 指定 wall 底部的高度数组。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定参考椭球体。</td>
</tr>
</tbody>
</table>
<hr>
<p>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/WallGeometry.html">WallGeometry</a></strong>、 <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/WallOutlineGeometry.html">WallOutlineGeometry</a></strong></p>
<h2>事件</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>每当更改或修改属性或子属性时触发该事件。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          geometry: null,
          attributes: null,
          vertexFormat: null,
          positions: [
            { lng: 107.0, lat: 40.0, height: 100000.0 },
            { lng: 97.0, lat: 40.0, height: 100000.0 },
            { lng: 97.0, lat: 37.0, height: 100000.0 },
            { lng: 107.0, lat: 37.0, height: 100000.0 },
            { lng: 107.0, lat: 40.0, height: 100000.0 }
          ],
          positionsOutline: [
            { lng: 115.0, lat: 30.0, height: 200000.0 },
            { lng: 90.0, lat: 30.0, height: 200000.0 }
          ]
        }
      },
      mounted() {
        Promise.all([this.$refs.wall.createPromise, this.$refs.wallOutline.createPromise]).then((instances) => {
          console.log('All geometries are loaded.')
          const boundingSphereUnion = instances.reduce((prev, cur) => {
            const geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
            const boundingSphere = cur.vm.$parent.modelMatrix
              ? Cesium.BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
              : geometry.boundingSphere
            return prev === null ? boundingSphere : Cesium.BoundingSphere.union(prev, boundingSphere)
          }, null)
          instances[0].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
        })
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.viewer = viewer
          const { PerInstanceColorAppearance, ColorGeometryInstanceAttribute } = Cesium
          this.appearance = new PerInstanceColorAppearance({
            flat: true
          })
          this.vertexFormat = PerInstanceColorAppearance.FLAT_VERTEX_FORMAT
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random(), 0.5)
          }
        },
        LEFT_CLICK(movement) {
          const feature = this.viewer.scene.pick(movement.position)
          console.log(feature)
        }
      }
    }
  </script>